
import Step from "../../../base/step/step.vue"
import Scroll from '../../../base/scroll/scroll.vue'
import Rater from 'vux/src/components/rater/index.vue'

export default {
name: "component_name",
components: {
Step,
Scroll,
Rater
},
data() {
return {
isOk: true,
starStyle: '', //
red: 'red', //
active: true, // or 
priceNum: 5 , //
goods1: 5, //1
goods2: 5, //2
}
},
methods: {
doNext() {

var that = this;
this.$axios.post('/api/orderOperate/backOrderSubmit', {
buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
commonImg: this.$route.query.commonImg,
goodsCommon: this.goods1,
worthStar: this.goods2
}).then((data) => {
console.log(data)
if(data.data.code === '200') {
this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate2' } })
} else {
this.$vux.alert.show({
title: '',
content: data.data.message,
})
}
}).catch((error) => {
this.$vux.alert.show({
title: '',
content: '',
})
})
}
}
}



